import * as types from "./sellerTypes";
import axios from "axios";
import * as config from "../../config";

export const addSellerRequest = () => {
    return {
      type: types.ADD_SELLER_REQUEST,
    };
  };
  
  export const addSellerSuccess = (message) => {
    return {
      type: types.ADD_SELLER_SUCCESS,
      payload: message,
    };
  };
  
  export const addSellerFailure = (errorMsg) => {
    return {
      type: types.ADD_SELLER_FAILURE,
      payload: errorMsg,
    };
  };
   
  export const insertSeller = (sellerData) => {
    return (dispatch) => {
      dispatch(addSellerRequest());
      axios
        .post(config.SERVER_URI+"/sellers/insertSeller",sellerData)
        .then((response) => {
          const data = response.data;
          dispatch(addSellerSuccess(response.data));
        })
        .catch((error) => {
          dispatch(addSellerFailure(error));
        });
    };
  };