import * as actionTypes from './categoryTypes'

const initialState={
    loading:true,
    categoryData:[],
    error:"",
    categoryInserted:false,
    categoryInsertedResult:[],
    plaincategoryLoading:true,
    plaincategoryData:[],
    plaincategoryError:""
}


const categoryReducer = (state = initialState,action)=>{
    switch(action.type){
        case actionTypes.GET_CATEGORIES_REQUEST:
            return{
                ...state,
                loading:true
            }
        case actionTypes.GET_CATEGORIES_SUCCESS:
            return{
                ...state,
                loading:false,
                categoryData:action.payload
            }
        case actionTypes.GET_CATEGORIES_FAILURE:
            return{
                ...state,
                loading:false,
                categoryData:[],
                error:action.payload
            }
        case actionTypes.INSERT_CATEGORIES_SUCCESS:
            return{
                ...state,
                categoryInserted:true,
                error:"",
                categoryInsertedResult:action.payload
            }    
       
        
        case actionTypes.INSERT_CATEGORIES_FAILURE:
                return{
                    ...state,
                    categoryInserted:false,
                    error:action.payload
                }    

        case actionTypes.GET_PLAIN_CATEGORIES_REQUEST:
            return{
                ...state,
                plaincategoryLoading:true,
                
            }

        case actionTypes.GET_PLAIN_CATEGORIES_SUCCESS:
            return{
                ...state,
                plaincategoryLoading:false,
                plaincategoryData:action.payload,
                plaincategoryError:""
            }

        case actionTypes.GET_PLAIN_CATEGORIES_FAILURE:
            return{
                ...state,
                plaincategoryLoading:false,
                plaincategoryData:[],
                plaincategoryError:action.payload
            }
        
        default:
            return state
    }
}

export default categoryReducer