import * as actionTypes from './sellerTypes'

const initialState={
    insertSellerLoading:true,
    insertSellerAdded:false,
    insertSellerSuccessMessage:"",
    insertSellerErrorMessage:"",
}

const sellerReducer = (state = initialState,action)=>{
    switch(action.type){
            case actionTypes.ADD_SELLER_REQUEST:
                return{
                    ...state,
                    insertSellerLoading:true
                }
            case actionTypes.ADD_SELLER_SUCCESS:
                return{
                    ...state,
                    insertSellerLoading:false,
                    insertSellerSuccessMessage:action.payload,
                    insertSellerErrorMessage:"",
                    insertSellerAdded:true,
                }
            case actionTypes.ADD_SELLER_FAILURE:
                return{
                    ...state,
                    insertSellerLoading:false,
                    insertSellerSuccessMessage:"",
                    insertSellerErrorMessage:action.payload,
                    insertSellerAdded:false
                }

        default:
            return state
    }
}

export default sellerReducer