import  React  from "react";
import { Form, Input, Button, Checkbox,Card } from 'antd';
import { UserOutlined, LockOutlined } from '@ant-design/icons';
import { styles } from "../../styles.css";
import "antd/dist/antd.css";
import axios from 'axios'
import  {Link,BrowserRouter,Redirect, Router}  from "react-router-dom";
import AdminSideBar from "../admin/sidebar";
import * as config from "../../config";
class Login extends React.Component{

    constructor(props){
        super(props);
        this.state = {
          navigate: false,
          referrer: null,
      }
    }

    onFinish = values => {
        axios.post(config.SERVER_URI+'/auth',
        { email: values.email,password:values.password })
        .then(data =>{
            localStorage.setItem('authToken',data.data.token)
            localStorage.setItem('email',data.data.user.email)
            localStorage.setItem('role',data.data.user.role)

            if (localStorage.getItem('role')==1) {
              this.setState({referrer: '/AdminHome/'});
            }
            else if (localStorage.getItem('role')==2){
              this.setState({referrer: '/VendorHome/'});
            }
            else{
              console.log('Could not identify')
            }
        })
        .catch(err=>console.log(err))
      };

      componentDidMount(){
        if(localStorage.getItem('authToken')){
          const role= localStorage.getItem('role')
          if(role==1){
            this.setState({referrer: '/AdminHome/'});
          }
          else if (localStorage.getItem('role')==2){
            this.setState({referrer: '/VendorHome/'});
          }
          else{
            console.log('Could not identify')
          }
        }
        // else{
        //   this.setState({referrer: '/login/'});

        // }
      }
   
      render() {
        const {referrer} = this.state;
        if (referrer) return <div><Redirect to={referrer} /></div>;

          return(
              <div className="LoginCard">
            <Card className="innerCard">
          <h2>Login</h2>
          <hr></hr>
          <br></br>
        <Form
          name="normal_login"
          className="login-form"
          initialValues={{ remember: true }}
          onFinish={this.onFinish}
        >
          <Form.Item
            name="email"
            rules={[{ required: true, message: 'Please input your E-Mail!' }]}
          >
            <Input prefix={<UserOutlined className="site-form-item-icon" />} placeholder="E-Mail" />
          </Form.Item>
          <Form.Item
            name="password"
            rules={[{ required: true, message: 'Please input your Password!' }]}
          >
            <Input
              prefix={<LockOutlined className="site-form-item-icon" />}
              type="password"
              placeholder="Password"
            />
          </Form.Item>
          <Form.Item>
            <Form.Item name="remember" valuePropName="checked" noStyle>
              <Checkbox>Remember me</Checkbox>
            </Form.Item>
    
            <a className="login-form-forgot" href="">
              Forgot password
            </a>
          </Form.Item>
    
          <Form.Item>
            <Button type="primary" htmlType="submit" className="login-form-button"
          >
              Log in
            </Button>
          </Form.Item>
        </Form>
        </Card>
        </div>
          )
      }
}

export default Login;