import * as types from "./productTypes";
import axios from "axios";
import * as config from "../../config";

export const getProductSuggestionRequest = () => {
  return {
    type: types.GET_PRODUCTS_SUGGESTION_REQUEST,
  };
};

export const getProductSuggestionSuccess = (products) => {
  return {
    type: types.GET_PRODUCTS_SUGGESTION_SUCCESS,
    payload: products,
  };
};

export const getProductSuggestionFailure = (errorMsg) => {
  return {
    type: types.GET_PRODUCTS_SUGGESTION_FAILURE,
    payload: errorMsg,
  };
};

export const addProductRequest = () => {
  return {
    type: types.ADD_PRODUCT_REQUEST,
  };
};

export const addProductSuccess = (message) => {
  return {
    type: types.ADD_PRODUCT_SUCCESS,
    payload: message,
  };
};

export const addProductFailure = (errorMsg) => {
  return {
    type: types.ADD_PRODUCT_FAILURE,
    payload: errorMsg,
  };
};

