import * as actionTypes from './productTypes'

const initialState={
    suggestionsloading:true,
    productSuggestionsData:[],
    productSuggestionError:"",
    insertProductLoading:true,
    insertProductAdded:false,
    insertProductSuccessMessage:"",
    insertProductErrorMessage:"",
    insertedProductData:{},
    inserImagesLoading:true,
    insertImagesSuccess:"",
    insertImagesError:"",
    getProductsAdminLoading:true,
    allProductsData:[],
    getProductsAdminError:"",
    totalProductCount:0,
    propsArr:[],
    singleProductLoading:true,
    singleProductData:{},
    singleProductError:""
}

const productReducer = (state = initialState,action)=>{
    switch(action.type){
        case actionTypes.GET_PRODUCTS_SUGGESTION_REQUEST:
            return{
                ...state,
                suggestionsloading:true
            }
        case actionTypes.GET_PRODUCTS_SUGGESTION_SUCCESS:
            return{
                ...state,
                suggestionsloading:false,
                productSuggestionsData:action.payload,
                productSuggestionError:""
            }
        case actionTypes.GET_PRODUCTS_SUGGESTION_FAILURE:
            return{
                ...state,
                suggestionsloading:false,
                productSuggestionsData:[],
                productSuggestionError:action.payload
            }


            case actionTypes.ADD_PRODUCT_REQUEST:
                return{
                    ...state,
                    insertProductLoading:true
                }
            case actionTypes.ADD_PRODUCT_SUCCESS:
                return{
                    ...state,
                    insertProductLoading:false,
                    insertProductSuccessMessage:action.payload,
                    insertProductErrorMessage:"",
                    insertProductAdded:true,
                    insertedProductData:action.payload
                }
            case actionTypes.ADD_PRODUCT_FAILURE:
                return{
                    ...state,
                    insertProductLoading:false,
                    insertProductSuccessMessage:"",
                    insertProductErrorMessage:action.payload,
                    insertProductAdded:false
                }

                case actionTypes.ADD_PRODUCT_IMAGES_REQUEST:
                    return{
                        ...state,
                        inserImagesLoading:true
                    }
                case actionTypes.ADD_PRODUCT_IMAGES_SUCCESS:
                    return{
                        ...state,
                        inserImagesLoading:false,
                        insertImagesSuccess:action.payload,
                        insertImagesError:"",
                    }
                case actionTypes.ADD_PRODUCT_IMAGES_FAILURE:
                    return{
                        ...state,
                        inserImagesLoading:false,
                        insertImagesError:action.payload,
                        insertImagesSuccess:""
                    }

                    case actionTypes.GET_PRODUCTS_ADMIN_REQUEST:
                        return{
                            ...state,
                            getProductsAdminLoading:true
                        }
                    case actionTypes.GET_PRODUCTS_ADMIN_SUCCESS:
                        return{
                            ...state,
                            getProductsAdminLoading:false,
                            allProductsData:action.payload.products,
                            getProductsAdminError:"",
                            totalProductCount:action.payload.totalCount,
                            propsArr:action.payload.propsArr
                        }
                    case actionTypes.GET_PRODUCTS_ADMIN_FAILURE:
                        return{
                            ...state,
                            getProductsAdminLoading:false,
                            getProductsAdminError:action.payload,
                            allProductsData:[]
                        }

                        case actionTypes.GET_SINGLE_PRODUCT_REQUEST:
                            return{
                                ...state,
                                singleProductLoading:true
                            }
                        case actionTypes.GET_SINGLE_PRODUCT_SUCCESS:
                            return{
                                ...state,
                                singleProductLoading:false,
                                singleProductData:action.payload,
                                singleProductError:""
                            }
                        case actionTypes.GET_SINGLE_PRODUCT_FAILURE:
                            return{
                                ...state,
                                singleProductLoading:false,
                                singleProductData:[],
                                singleProductError:action.payload
                            }
        
        default:
            return state
    }
}

export default productReducer