import * as types from "./categoryTypes";
import axios from "axios";
import * as config from "../../config";

export const getCategoryDataRequest = () => {
  return {
    type: types.GET_CATEGORIES_REQUEST,
  };
};

export const getCategoryDataSuccess = (categories) => {
  return {
    type: types.GET_CATEGORIES_SUCCESS,
    payload: categories,
  };
};

export const getCategoryDataFailure = (errorMsg) => {
  return {
    type: types.GET_CATEGORIES_FAILURE,
    payload: errorMsg,
  };
};

export const insertCategoryDataSuccess = (data) => {
  return {
    type: types.INSERT_CATEGORIES_SUCCESS,
    payload: data,
  };
};

export const insertCategoryDataFailure = (err) => {
    return {
      type: types.INSERT_CATEGORIES_FAILURE,
      payload: err,
    };
  };

  export const getPlainCategoryDataRequest = () => {
    return {
      type: types.GET_PLAIN_CATEGORIES_REQUEST,
    };
  };
  
  export const getPlainCategoryDataSuccess = (categories) => {
    return {
      type: types.GET_PLAIN_CATEGORIES_SUCCESS,
      payload: categories,
    };
  };
  
  export const getPlainCategoryDataFailure = (errorMsg) => {
    return {
      type: types.GET_PLAIN_CATEGORIES_FAILURE,
      payload: errorMsg,
    };
  };
  

export const fetchCategoryData = () => {
  return (dispatch) => {
    dispatch(getCategoryDataRequest());
    axios
      .get(config.SERVER_URI+"/customerHome/GetNavbarData")
      .then((response) => {
        const data = response.data;
        console.log(response);
        dispatch(getCategoryDataSuccess(data));

      })
      .catch((error) => {
        dispatch(getCategoryDataFailure(error.message));
      });
  };
};

export const insertCategoryData = (categoryArr,callback) => {
    return (dispatch) => {
      axios
        .post(config.SERVER_URI+"/category/insertCategory",categoryArr)
        .then((response) => {
          const data = response.data;
          console.log(response);
          dispatch(insertCategoryDataSuccess(data));
          callback();
        })
        .catch((error) => {
          dispatch(insertCategoryDataFailure(error.response.data));
        });
    };
  };

  export const fetchPlainCategoryData = () => {
    return (dispatch) => {
      dispatch(getPlainCategoryDataRequest());
      axios
        .get(config.SERVER_URI+"/category/getAllCategories")
        .then((response) => {
          const data = response.data;
          console.log(response);
          dispatch(getPlainCategoryDataSuccess(data));
  
        })
        .catch((error) => {
          dispatch(getPlainCategoryDataFailure(error.message));
        });
    };
  };